<template>
  <div class="pos"> 
    <el-row>
      <el-col :span='7' class="pos-order" id="order-list">
      <!-- 我是订单栏 -->
      <!-- el-tabs就是element的多标签组件 -->
        <el-tabs class="table-a">
        <el-tab-pane label="点餐"> 
          <el-table  :data="tableData" border style="width:100%">
             <el-table-column prop="goodsName" label="商品名称"></el-table-column>
             <el-table-column prop="count" label="数量" width="50"></el-table-column>
             <el-table-column prop="price" label="金额" width="70"></el-table-column>
             <el-table-column  label="操作" width="100" fixed="right">
              <template slot-scope="scope"> 
                  <el-button type="text" size="small"  @click="deleteSingleGoods(slot-scope.row)">删除</el-button>
                  <el-button type="text" size="small"  @click="addOrderList(slot-scope.row)">增加</el-button>
                </template>
             </el-table-column>
          </el-table>
          <div class="totalDiv">
              <span><small>数量：</small>{{totalCount}}</span> 
              <span><small>金额：</small>￥ {{totalMoney}} 元</span>
          </div>
          <div class="div-btn"> 
            <el-button type="warning" size="small">挂单</el-button>
            <el-button type="danger" size="small"  @click="deleteAllGoods()">删除</el-button>
            <el-button type="success" size="small" @click="checkOut()">结账</el-button>
          </div>
          </el-tab-pane>
        <el-tab-pane label="挂单">
            挂单
        </el-tab-pane>
        <el-tab-pane label="外卖">
            外卖
        </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col  :span='17' class="goods-all">
        <div class="ofen-goods">
          <div class="title">常用商品</div>
          <div class="ofen-goods-list">
            <ul class='goodsList'>
              <li v-for="goods in oftenGoods"  @click="addOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="o-price">￥{{goods.price}}元</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-type">
          <el-tabs class="table-a">
            <el-tab-pane label="汉堡">
              <div>
                <ul class='cookList'>
                   <li v-for="goods in type0Goods"  @click="addOrderList(goods)">
                      <span class="foodImg"><img :src="goods.goodsImg" height="70px" ></span>
                      <span class="foodName">{{goods.goodsName}}</span>
                      <span class="foodPrice">￥{{goods.price}}元</span>
                  </li>
              </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="小食">
            小食
            </el-tab-pane>
            <el-tab-pane label="饮料">
            饮料
            </el-tab-pane>
            <el-tab-pane label="套餐">
            套餐
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'pos',
  data(){
    return{
      tableData:[],
      oftenGoods:[
      {
        goodsId:1,
        goodsName:'香辣鸡腿堡',
        price:18
      },
      {
        goodsId:2,
        goodsName:'田园鸡腿堡',
        price:15
      },
      {
        goodsId:3,
        goodsName:'和风汉堡',
        price:15
      },
      {
        goodsId:4,
        goodsName:'快乐全家桶',
        price:80
      },
      {
        goodsId:5,
        goodsName:'脆皮炸鸡腿',
        price:10
      },
      {
        goodsId:6,
        goodsName:'魔法鸡块',
        price:20
      },
      {
        goodsId:7,
        goodsName:'可乐大杯',
        price:10
      },
      {
        goodsId:8,
        goodsName:'雪顶咖啡',
        price:18
      },
      {
        goodsId:9,
        goodsName:'大块鸡米花',
        price:15
      },
      {
        goodsId:10,
        goodsName:'香脆鸡柳',
        price:17
      }
      ],
      type0Goods:[
          {
              goodsId:1,
              goodsImg:"../../../static/img/01.jpg",
              goodsName:'香辣鸡腿堡',
              price:18
          }, {
              goodsId:2,
              goodsImg:"../../../static/img/02.jpg",
              goodsName:'田园鸡腿堡',
              price:15
          }, {
              goodsId:3,
              goodsImg:"../../../static/img/03.jpg",
              goodsName:'和风汉堡',
              price:15
          }, {
              goodsId:4,
               goodsImg:"../../../static/img/04.jpg",
              goodsName:'快乐全家桶',
              price:80
          }, {
              goodsId:5,
               goodsImg:"../../../static/img/05.jpg",
              goodsName:'脆皮炸鸡腿',
              price:10
          }, {
              goodsId:6,
               goodsImg:"../../../static/img/06.jpg",
              goodsName:'魔法鸡块',
              price:20
          }, {
              goodsId:7,
               goodsImg:"../../../static/img/07.jpg",
              goodsName:'可乐大杯',
              price:10
          }, {
              goodsId:8,
               goodsImg:"../../../static/img/08.jpg",
              goodsName:'雪顶咖啡',
              price:18
          }, {
              goodsId:9,
               goodsImg:"../../../static/img/09.jpg",
              goodsName:'大块鸡米花',
              price:15
          }, {
              goodsId:10,
               goodsImg:"../../../static/img/10.jpg",
              goodsName:'香脆鸡柳',
              price:17
          }

      ],
      totalMoney:0,
      totalCount:0,
    }
  },
  mounted:function(){
    // js原生方法
    var orderHeight=document.body.clientHeight; 
    console.log('高度是'+orderHeight);
    document.getElementById('order-list').style.height=orderHeight+'px';
  },
  methods:{
    addOrderList(goods){
      // 先对钱和数量进行循环
      this.totalMoney=0;
      this.totalCount=0;
      //商品是否已经存在于订单列表中
      let isHave = false;
      for(let i= 0;i<this.tableData.length;i++){
        if(this.tableData[i].goodsId == goods.goodsId){
          isHave = true;
        }
      }

      //根据判断的值编写业务逻辑
      if(isHave){
        //改变列表中商品的数量 
        let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
        arr[0].count++;
      }else{
        //声明新商品 因为传进来的goods和列表中的goods是不一样的
        let newGoods= {
          goodsId:goods.goodsId,
          goodsName:goods.goodsName,
          price:goods.price,
          count:1
        };
        // 数组压入
        this.tableData.push(newGoods);
      }
      this.getAllMoney();
    },

    //删除单个商品 删除单品
    deleteSingleGoods(goods){
      // 筛选出除了这个商品的其他商品 删除之后要对数量和金额重新计算
      this.tableData=this.tableData.filter(o=>o.goodsId !=goods.goodsId);
      this.getAllMoney();
    },
    deleteAllGoods(){
      this.tableData=[];
      this.totalMoney=0;
      this.totalCount=0;
    },
    //模拟结账
    checkOut(){
      if(this.totalCount!=0){
        this.tableData=[];
        this.totalMoney=0;
        this.totalCount=0;
        this.$message({
          message:'结账成功，感谢你又为店里出了一份力',
          type:'success'
        });
      }else{
        this.$message.error('不能空结，老板了解你急切的心情!')
      }
    },
    // 汇总数量金额
    getAllMoney(){
       this.totalMoney=0;
       this.totalCount=0;
       if(this.tableData){
         // 计算汇总金额和数量
        this.tableData.forEach((element)=>{
        this.totalCount+=element.count;
        this.totalMoney=this.totalMoney+(element.price*element.count);
      })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos-order{
  background-color: #f9fafc;
  border-right:1px solid #c0ccda;
  /*这个高不会起作用 要在js中实现*/
  height: 100%; 
}
.div-btn{
  margin-top:10px;
}
.title{
  height: 20px;
  border-bottom:1px solid #d3dce6;
  background-color:#f9fafc;
  padding: 10px;
  text-align: left;
}
.goods-type{
  /*清除浮动*/
  clear:both;
  border-top:1px solid #d3dce6;
  height: auto;

}
.table-a{
  margin-left:10px;
}
.ofen-goods-list ul li{
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 10px;
  background-color: #fff;
}
.o-price{
  color:#58b7ff;
}
.goodsList li{
    cursor: pointer;
}
/*图片的样式*/
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor: pointer;

   }
   .cookList li span{

        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;

   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .totalDiv{
    background-color: #fff;
    padding: 10px;
    border-bottom:1px solid  #d3dce6;
   }
   .totalDiv span{
    margin-right: 20px;
   }
</style>

<!-- element是虚拟dom -->


